                    <div
                        class="video-btn"
                        @click="CGstart(roleInfo.ext[29].value[0].url)"
                    ></div>
                </div>
                <popup v-model:show="CGshow">
                    <video
                        :preload="preload"
                        :width="width"
                        :height="height"
                        align="center"
                        :controls="controls"
                        :autoplay="autoplay"
                        loop="true"
                        ref="CGRef"
                        v-if="CGshow"
                    >
                        <source :src="videoSrc" type="video/mp4" />
                    </video>
                </popup>
            </div>
        </div>
        <div class="role-box">
            <p>{{ $route.query.roleName }}</p>
            <div class="roles">
                <div
                    v-for="(item, index) of roles"
                    :key="index"
                    :class="{ actived: actived == index }"
                    class="role-btn"
                    @click="changeRole(item, index, $event)"
                >
                    <div class="imgbox">
                        <img v-lazy="item.ext[4].value[0].url" />
                    </div>
                    <p>{{ item.ext[0].value }}</p>
                </div>
            </div>
        </div>
        <page-footer />
    </page-view>
</template>

<script>
import { ref, reactive, toRefs, watch, getCurrentInstance } from "vue";
import { useRoute, useRouter } from "vue-router";
import { Popup } from "vant";
import Api from "@/utils/api";
export default {
    name: "RoleDetail",
    components: {
        Popup,
    },
    setup() {
        const route = useRoute();
        const router = useRouter();
        const showStart = ref(false);
        const actived = ref(0);
        const { proxy } = getCurrentInstance();
        const info = reactive({
            roleInfo: {},
            roles: {},
        });
        // console.log(route.query.title);
        const getRoleInfo = async () => {
            let results = await Api.getInfo.getInfos({
                pageSize: 200,
                pageNum: 1,
                channelId: 181,
            });
            let rolesinfo = await Api.getInfo.getInfos({
                pageSize: 200,
                pageNum: 1,
                channelId: 183,
            });

            info.roleInfo = results.list.filter((item) => {
                return item.ext[0].value == route.query.title;
            });
            // console.log(results);
            info.roleInfo = info.roleInfo[0];

            let a = rolesinfo.list.filter((item) => {
                return item.ext[0].value == route.query.roleName;
            });

            info.roles = results.list.filter((item) => {
                return item.type == a[0].type;
            });

            info.roles.forEach((item, index) => {
                if (item.title == route.query.title) {
                    actived.value = index;
                }
            });

            showStart.value = true;
            // console.log(info.roles);
            // console.log(info.roleInfo);
        };
        getRoleInfo();
        const CGshow = ref(false);

        const CGstart = (url) => {
            playerOptions.videoSrc = url;
            CGshow.value = true;
        };

        const playerOptions = reactive({
            videoSrc: "",
            width: "375", // 设置视频播放器的显示宽度（以像素为单位）
            height: "210",
            preload: "auto", //  建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
            controls: true, // 确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
            autoplay: "",
        });

        const changeRole = (item, index, e) => {
            console.log(item);
            actived.value = index;
            router.push({
                name: "roleDetail",
                query: {
                    title: item.ext[0].value,
                    roleName: route.query.roleName,
                },
            });

            e.target.parentElement.parentElement.parentElement.parentElement.parentElement.scrollTop = 0;
        };
        watch(
            () => route.query,
            () => {
                if (route.query.title != undefined) {
                    getRoleInfo();
                }
            }
        );
        return {
            ...toRefs(info),
            showStart,
            actived,
            ...toRefs(playerOptions),
            CGshow,
            CGstart,
            changeRole,
        };
    },
};
</script>

